html {
  background: #FAF7F2;
  background-image: url(https://s3.postimg.org/s1n3ji1ur/paper_fibers_2_X.png);
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
  font-size: 14px;
  font-weight: 400;
}

*, *:before, *:after {
  box-sizing: inherit;
}
.u-clearfix:before,
.u-clearfix:after {
  content: " ";
  display: table;
}

.u-clearfix:after {
  clear: both;
}

.u-clearfix {
  *zoom: 1;
}

.subtle {
  color: #aaa;
}

.bcard-container {
  margin: 16% auto 0%;
  position: relative;
  width: 80%;
  height: 400px;
}

.bcard {
  background-color: #fafafd;
  padding: 5% 0% 0% 15%;
  position: relative;
  box-shadow: 0 0 5px rgba(75, 75, 75, .07);
  z-index: 1;
}

.bcard-body {
  display: inline-block;
  float: left;
  width: 50%;
}

.bcard-number {
  margin-top: 15px;
}

.bcard-circle {
  border: 1px solid #aaa;
  border-radius: 50%;
  display: inline-block;
  line-height: 22px;
  font-size: 12px;
  height: 25px;
  text-align: center;
  width: 25px;
}

.bcard-author {
  display: block;
  font-size: 12px;
  letter-spacing: .5px;
  margin: 15px 0 0;
  text-transform: uppercase;
}

.bcard-title {
  font-family: 'Cormorant Garamond', serif;
  font-size: 60px;
  font-weight: 500;
  line-height: 60px;
  margin: 10px 0;
}

.bcard-description {
  display: inline-block;
  font-weight: 300;
  line-height: 22px;
  margin: 10px 0;
}

.bcard-read {
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 6px;
  margin: 15px 0 10px;
  position: relative;
  text-align: right;
  text-transform: uppercase;
}

.bcard-read:after {
  background-color: #b8bddd;
  content: "";
  display: block;
  height: 1px;
  position: absolute;
  top: 9px;
  width: 75%;
}

.bcard-tag {
  float: right;
  margin: 5px 0 0;
}

.bcard-media {
  float: right;
  width: 30%;
  position: relative;
  margin-right: 5%;
}

.bcard-shadow {
  background-color: #fff;
  box-shadow: 0 2px 25px 2px rgba(0, 0, 0, 1), 0 2px 50px 2px rgba(0, 0, 0, 1), 0 0 100px 3px rgba(0, 0, 0, .25);
  height: 1px;
  margin: -1px auto 0;
  width: 80%;
  z-index: -1;
}

